@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
* {
  font-family: "Source Sans", "Source Han Serif TC";
}

body {
  margin: 0;
}

h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 125%;
}

h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 125%;
}

h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 125%;
}

h4 {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 125%;
}

h5 {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 125%;
}

p.body1 {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
}

nav {
  width: 100%;
  height: 64px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 5;
  background-color: white;
}

nav .logo {
  margin-left: 48px;
}

nav .tags {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-right: 64px;
}

nav .tags h5 {
  margin-bottom: 0;
}

nav .tags .nav-tag {
  margin-left: 24px;
}

nav .tags Button {
  margin-left: 12px;
}

@media only screen and (max-width: 768px) {
  nav .logo {
    object-fit: cover;
  }
}

#about {
  width: 100%;
}

#about .about-header {
  position: relative;
  width: 100%;
  height: 62vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  padding-top: 64px;
  color: white;
  overflow: hidden;
  background-image: url("https://images.unsplash.com/photo-1510146758428-e5e4b17b8b6a?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80");
  background-position: center;
  background-repeat: none;
  background-size: cover;
}

#about .about-header h1,
#about .about-header h3 {
  color: white;
  z-index: 2;
}

#about .about-header h3 {
  padding: 0px 24px;
}

#about .about-header::before {
  content: "";
  position: absolute;
  z-index: 1;
  opacity: 0.3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

#about .logo-container {
  padding: 20vh 0px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

#about .logo-container .school-logo {
  margin: 24px 24px;
  max-height: 60px;
}

#about .found-purpose {
  width: 100%;
  height: 46.8vh;
  background-color: grey;
  display: flex;
  margin-bottom: 23vh;
}

#about .found-purpose .purpose-container {
  width: calc(100% * 7 / 12);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  padding-left: 96px;
  padding-right: calc(100% * 1 / 12);
}

#about .found-purpose .purpose-container h2 {
  margin-top: 0;
}

#about .found-purpose .purposeImg {
  width: calc(100% * 5 / 12);
  height: 100%;
  background-color: #fff;
  background-image: url("https://images.unsplash.com/photo-1600195077909-46e573870d99?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80");
  background-position: center;
  background-repeat: none;
  background-size: cover;
}

#about .team-intro {
  padding: 0px 96px;
}

#about .team-intro .intro-content-container {
  margin-bottom: 96px;
}

#about .team-intro .intro-content-container p {
  width: 80%;
}

#about .team-intro .members {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#about .team-intro .members .member-column {
  width: 45%;
  height: 100%;
}

#about .team-intro .members .member-column .member-data {
  min-height: 170px;
  display: flex;
  margin-bottom: 64px;
}

#about .team-intro .members .member-column .member-data .member-photo {
  width: 170px;
  height: 170px;
  background-position: center;
  background-repeat: none;
  background-size: cover;
  min-width: 170px;
  margin: 0;
}

#about .team-intro .members .member-column .member-data .member-description {
  max-height: 100%;
  box-sizing: border-box;
  padding-left: 48px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#about .team-intro .members .member-column .member-data .member-description h4 {
  margin: 0;
}

#about .team-intro .members .member-column .member-data .member-description p {
  margin-bottom: 0;
}

@media only screen and (max-width: 992px) {
  #about .logo-container {
    padding: 10vh 0vh;
  }
  #about .team-intro .intro-content-container {
    margin-bottom: 48px;
  }
  #about .team-intro .members {
    flex-direction: column;
  }
  #about .team-intro .members .member-column {
    width: 100%;
    height: 100%;
  }
  #about .team-intro .members .member-column .member-data {
    margin-bottom: 32px;
  }
  #about .team-intro .members .member-column .member-data .member-description {
    padding-left: 24px;
  }
}

@media only screen and (max-width: 768px) {
  #about .found-purpose {
    height: 80vh;
    flex-direction: column-reverse;
  }
  #about .found-purpose .purpose-container {
    width: calc(100%);
    height: calc(40%);
    padding-left: calc(100% * 1 / 12);
    padding-right: calc(100% * 1 / 12);
  }
  #about .found-purpose .purposeImg {
    width: calc(100%);
    height: calc(60%);
  }
  #about .team-intro {
    padding: 0px 24px;
  }
  #about .team-intro .intro-content-container p {
    width: 100%;
  }
}

#login {
  width: 100%;
  height: 100vh;
}

#login .login-container {
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto auto;
  width: 28vw;
  min-width: 330px;
  height: 60vh;
  border-radius: 5%;
  box-shadow: 3px 3px 10px 1px rgba(0, 0, 0, 0.2);
}

#login .login-container .login-form {
  width: 80%;
}

#login .login-container .login-logo {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  background-color: grey;
  margin-bottom: 24px;
}

#login .login-container .register-logo {
  width: 150px;
  height: 100px;
  border-radius: 10%;
  background-color: grey;
  margin-bottom: 24px;
}

#login .login-type {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0 24px;
}

#login .login-type .types {
  display: flex;
  flex-direction: row;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 100%;
  margin-bottom: 64px;
}

#login .login-type .types .login-as-student-btn {
  margin-right: 24px;
}

#login .login-type .types .type {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#login .login-type .types .type .type-logo {
  width: 160px;
  height: 160px;
  background-color: grey;
  border-radius: 100%;
  margin-bottom: 16px;
}

#login .login-type .not-member {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#login .login-type .not-member h5 {
  margin: 0;
  margin-bottom: 12px;
}

@media only screen and (max-width: 414px) {
  #login .login-container {
    margin: auto 24px;
  }
}

#account-management {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

#account-management .management-container {
  min-height: 50%;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-top: 10%;
  box-sizing: border-box;
  padding: 2%;
  border-radius: 20px;
  box-shadow: 3px 3px 10px 1px rgba(0, 0, 0, 0.2);
}

#account-management .management-container h1 {
  margin-bottom: 24px;
}

#account-management .management-container .dash-board {
  width: 100%;
  height: 100%;
}

#account-management .management-container .dash-board .memberData-container {
  display: flex;
}

#account-management .management-container .dash-board .memberData-container ul {
  width: 100%;
}

#account-management .management-container .btn-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  margin-bottom: 16px;
}

#account-management .management-container .btn-container .btn {
  margin-right: 16px;
}
